.header-title{
  text-align: center;
  margin: 0 0 30px 0;

}
.event-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  .event-item{
    position: relative;
    width: 250px;
    height: 250px;
    margin: 10px;
    border: 1px solid grey;

    img{
      height: 100%;
      width: 100%;
    }

    .event-price{
      position: absolute;
      top: 0;
      left: 0;
      padding: 10px;
      font-weight: 300;
      font-size: .9em;
      background-color: rgba(0, 0, 0, 0.5);
      color: white !important;

      span{
        font-size: 1.5em;
        font-weight: 400;
      }
    }
    .event-places{
      position: absolute;
      top: 0;
      right: 0;
      padding: 10px 5px 10px 5px;
      margin: 5px;
      border-radius: 10%;
      font-weight: 300;
      font-size: .8em;
      background-color: #688E26;
      color: white !important;
    }
    .event-description{
      position: absolute;
      height: 50px;
      width: 100%;
      bottom: 0;
      left: 0;
      background-color: rgba(38,50,64, 0.9);
      color: red;
      display: block;
    }
    .event-description-content {
      .user-avatar{
        position: relative;
        top: 5px;
        left: 5px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }

      .user-name{
        position: absolute;
        bottom: 17px;
        left: 55px;
        font-size: .8em;
        color: white !important;
      }

      .user-location{
        position: absolute;
        bottom: 35px;
        right: 5px;
        color: white !important;
        font-size: .7em;
        font-weight: 200;
      }

    }

    .event-title{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
      display: none;
      text-align: center;

      a{
        &:hover{
          text-decoration: none;
        }
      }

      h3 {
        line-height: 230px;
        font-size: 1.2em;
        a {
          color: white !important;
        }
      }

      .icon-add{
        color: white !important;
        font-size: 100px;
        line-height: 0;
        margin: 50px  0 0 0;
      }

      .default-title{
        color: white !important;
      }

    }

    &:hover .event-title{
      display: block;
    }

  }

}



